:root {
  --green: #28b34b;
  --gray: #999999;
}

body {
  font-family: "MullerM", sans-serif;
}

.navbar {
  background-color: #fff;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 20;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  /* max-width: 1500px; */
}

.main-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
}

.nav-logo {
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  flex-grow: 1;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: var(--gray);
  text-decoration: none;
  padding: 0.2rem 1rem;
  height: 100%;
}
.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

/* .nav-item:hover:after {
  width: 100%;
  background: #ffdd40;
} */

.nav-item .active {
  color: #fff;
  background-color: var(--green);
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 1pxsolid #fff;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #1f5156;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-item .active {
    color: #ffdd40;
    border: none;
  }
  .nav-links {
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #ffdd40;
    width: 26px;
    height: 26px;
  }
}

.card {
  background: white;
  text-decoration: none;
  color: #444;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  min-height: 100%;
  /* height: 300px; */
  position: relative;
  transition: all 0.1s ease-in;
}
.card:hover {
  top: -2px;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}
.card article {
  padding: 18px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card h1 {
  font-size: 16px;
  margin: 0;
  color: #333;
}
.card p {
  flex: 1;
  line-height: 1.4;
  color: #999;
  margin: 0;
}
.card span {
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin: 1em 0 1em 0;

  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;

  color: #999999;
}
.card .thumb {
  height: 170px;
  /* padding-bottom: 60%; */
  background-size: cover;
  background-position: center center;
}

.card.hor {
  background: white;
  text-decoration: none;
  color: #444;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row;
  min-width: 100%;
  height: 190px;
  min-height: auto;
  /* height: 300px; */
  position: relative;
  transition: all 0.1s ease-in;
  margin: 8px 0;
}

.card.hor .thumb {
  width: 120px;
  height: 100%;
  /* padding-bottom: 60%; */
  background-size: cover;
  background-position: center center;
}

footer {
  background-color: var(--green);
  padding: 45px 0 20px;
  font-size: 15px;
  line-height: 24px;
  color: #fff;
}

footer h6
{
  color:#fff;
  font-size:16px;
  text-transform:uppercase;
  margin-top:5px;
  letter-spacing:2px
}

.footer-links {
  padding-left: 0;
  list-style: none;
}
.footer-links li {
  display: block;
}
.footer-links a {
  color: #fff;
}
.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
  color: #fff;
  text-decoration: none;
}
.footer-links.inline li {
  display: inline-block;
}
